<template>
    <div class="box">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" :class="[demo-ruleForm, {'validateForm': hasValidate}]">
            <el-form-item label="活动名称" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="活动区域" prop="region">
                <el-select v-model="ruleForm.region" placeholder="请选择活动区域">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="活动时间" required>
                <el-col :span="11">
                <el-form-item prop="date1">
                    <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                <el-form-item prop="date2">
                    <el-time-picker type="fixed-time" placeholder="选择时间" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
                </el-form-item>
                </el-col>
            </el-form-item>
            <el-form-item label="即时配送" prop="delivery">
                <el-switch v-model="ruleForm.delivery"></el-switch>
            </el-form-item>
            <el-form-item label="活动性质" prop="type">
                <el-checkbox-group v-model="ruleForm.type">
                <el-checkbox label="美食/餐厅线上活动" name="type"></el-checkbox>
                <el-checkbox label="地推活动" name="type"></el-checkbox>
                <el-checkbox label="线下主题活动" name="type"></el-checkbox>
                <el-checkbox label="单纯品牌曝光" name="type"></el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="特殊资源" prop="resource">
                <el-radio-group v-model="ruleForm.resource">
                <el-radio label="线上品牌商赞助"></el-radio>
                <el-radio label="线下场地免费"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="活动形式" prop="desc">
                <el-input type="textarea" v-model="ruleForm.desc"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    data () {
        return {
            ruleForm: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
                date1: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                date2: [
                    { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
                ],
                type: [
                    { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                ],
                resource: [
                    { required: true, message: '请选择活动资源', trigger: 'change' }
                ],
                desc: [
                    { required: true, message: '请填写活动形式', trigger: 'blur' }
                ]
            },
            hasValidate: false
        }
    },
    methods: {
        submitForm (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.hasValidate = false
                    alert('submit!')
                } else {
                    console.log('error submit!!')
                    this.hasValidate = true
                    return false
                }
            })
        },
        resetForm (formName) {
            this.hasValidate = false
            this.$refs[formName].resetFields()
        }
    }
}
</script>
<style>
.box {
    width: 40%;
}
.line {
    text-align: center;
}
.el-radio__input.is-checked .el-radio__inner {
    border-color: #00C4C0;
    background: #00C4C0;
}
.el-radio__label {
    font-size: 12px;
    padding-left: 8px;
}
.el-radio__inner:hover {
    border-color: #00C4C0;
}
.el-radio__input.is-checked+.el-radio__label {
    color: #333;
}
.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #00C4C0;
    border-color: #00C4C0;
}
.el-checkbox__inner:hover {
    border-color: #00C4C0;
}
.el-checkbox__input.is-focus .el-checkbox__inner {
    border-color: #00C4C0;
}
/*label样式覆盖*/
.el-checkbox__input.is-checked+.el-checkbox__label {
    color: #333;
}
/*半选状态样式覆盖*/
.el-checkbox__input.is-indeterminate .el-checkbox__inner::before {
    height: 4px;
    top: 4px;
}
.el-date-table td.today span {
    color: #00C4C0;
    font-weight: 700;
}
.el-date-table td.current:not(.disabled) span {
    color: #fff;
    background-color: #00C4C0;
}
.el-picker-panel__icon-btn:hover {
    color: #00C4C0;
}
.el-date-picker__header-label.active,
.el-date-picker__header-label:hover {
    color: #00C4C0;
}
.el-picker-panel__shortcut:hover {
    color: #00C4C0;
}
.el-date-table td.available:hover {
    color: #00C4C0;
}
.el-date-table td span {
    border-radius: 2px;
}
.el-select .el-input {
    width: 130px;
}
.input-with-select .el-input-group__prepend {
    background-color: #fff;
}
/*input textarea__inner样式覆盖*/
.el-textarea__inner:focus {
    outline: 0;
    border-color: #00C4C0;
}
/*input样式覆盖*/
.el-input__inner {
    height: 32px;
    line-height: 32px;
    font-size: 12px;
}
.el-input.is-active .el-input__inner,
.el-input__inner:focus {
    border-color: #00C4C0;
    outline: 0;
}
/*input-icon 样式覆盖*/
.el-input__icon {
    line-height: 32px;
}
.el-select .el-input.is-focus .el-input__inner {
    border-color: #00C4C0;
}
.el-select .el-input__inner:focus {
    border-color: #00C4C0;
}
.el-select-dropdown__item.selected {
    color: #00C4C0;
    font-weight: 700;
}
/*下拉选中时的字体颜色修改*/
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected {
    color: #00C4C0;
    background-color: #fff;
}
.el-input__inner {
    height: 32px;
    font-size: 12px;
}
.el-input__icon {
    line-height: 32px;
}
/*下啦hover的背景色修改*/
.el-select-dropdown__item.hover, .el-select-dropdown__item:hover {
    background-color: #1acac6;
}
.el-form-item {
    transition: all .4s ease;
    margin-bottom: 5px;
}
.validateForm .el-form-item {
    margin-bottom: 20px;
}
</style>



